body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.App {
  display: flex;
  flex-direction: column;
  width: 200px;
  height: auto;
  box-sizing: border-box;
  background: #3d4549;
  border-radius: 6px;
  margin: 40px auto;
  text-align: center; }

.wrapper--numbers, .wrapper--operator {
  display: flex;
  flex-direction: row;
  width: 65%;
  margin: auto;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px; }

.wrapper--counter {
  display: flex;
  flex-direction: row;
  width: 80%;
  overflow: hidden;
  border-radius: 5px;
  font-family: monospace;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  height: 50px;
  background-color: lightgray; }

button:focus {
  outline: 0; }

.wrapper--numbers > button {
  flex-basis: calc( 200px * 0.65 / 3 - 12px);
  font-size: 18px;
  padding: 3px;
  background-color: #313131;
  border: 0.5px solid lightyellow;
  cursor: pointer;
  color: lightblue;
  margin: 5px;
  font-family: monospace;
  justify-items: flex-start;
  -webkit-transition: box-shadow 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out; }

.wrapper--numbers > button:hover {
  border-color: #aaa;
  box-shadow: 0px 2px 6px lightblue; }

.wrapper--numbers > button:active {
  -webkit-transform: translateY(0.5px);
          transform: translateY(0.5px);
  box-shadow: 0px 1px 4px lightblue; }

.wrapper--operator > button {
  flex-basis: calc( 200px * 0.65 / 3 - 12px);
  font-size: 18px;
  padding: 3px;
  background-color: lightblue;
  border: 0.5px solid black;
  cursor: pointer;
  margin: 5px; }

.wrapper--operator > button:last-child {
  background-color: orange; }

.btn-order[data-order="1"] {
  order: 1; }

.btn-order[data-order="2"] {
  order: 2; }

.btn-order[data-order="3"] {
  order: 3; }

.btn-order[data-order="4"] {
  order: 4; }

.btn-order[data-order="5"] {
  order: 5; }

.btn-order[data-order="6"] {
  order: 6; }

.btn-order[data-order="7"] {
  order: 7; }

.btn-order[data-order="8"] {
  order: 8; }

.btn-order[data-order="9"] {
  order: 9; }

.btn-order[data-order="10"] {
  order: 10; }

.btn-order[data-order="11"] {
  order: 11; }

h2 {
  text-align: right;
  width: inherit;
  font-size: 22px; }
  h2 > span:nth-child(2) {
    padding-left: 5px;
    padding-right: 5px; }

